<template>
  <apexchart
    ref="chart"
    type="line"
    width="90%"
    height="550"
    :options="options"
    :series="series"
  ></apexchart>
</template>
<script>
import VueApexCharts from "vue-apexcharts";

export default {
  props: { options: { required: true }, series: { required: true } },
  components: { apexchart: VueApexCharts },

  methods: {
    //* ============================================
    // X
    //  
    //* ============================================
    setXLabel: function () {
      this.$nextTick(() => {
        const chart = this.$refs.chart.chart; // Vue2chart
        chart.zoomX(
          chart.w.globals.minX, // X
          chart.w.globals.maxX // X
        );
      });
    },
  },
};
</script>