<template>
  <v-app>
    <v-container>
      <Ph2GraphArea graphType="2" ref="chr" />
    </v-container>
  </v-app>
</template>
  
  <script>
import { RawDataSettings } from "@/components-v1/parts/graph//.js";
import { RawDataGraphPanel } from "@/components-v1/parts/graph//.js";
import Ph2GraphArea from "@/components-v1/parts/graph/Ph2GraphTab.vue";
import { useSensoreData } from "@/api/SensorDataAPI.js";
import { LabelFlags } from "@/components-v1/parts/graph/.js";

export default {
  data() {
    return {
      yTitle: [
        { text: "()" },
        { text: "(RH)" },
        { text: "(W/)" },
        { text: "1(g/h)" },
        { text: "(m)" },
        { text: "(raw counts)" },
        { text: "(pF)" },
        { text: "()" },
        { text: "(vol%)" },
        { text: "2(g/h)" },
      ],

      xTitle: [{ text: "" }],
      flags: null,
    };
  },
  components: {
    Ph2GraphArea,
  },
  methods: {
    //* --------------------------------------------
    //* 
    //* --------------------------------------------
    setGraphData: function (
      titlePaths,
      deviceId,
      contentId,
      sensorId,
      startDate,
      endDate,
      interval,
      name,
      title
    ) {
      this.$nextTick(
        function () {
          this.setSonsorData(
            titlePaths,
            deviceId,
            contentId,
            sensorId,
            startDate,
            endDate,
            interval,
            name,
            title
          );
        }.bind(this)
      );
    },
    setSonsorData(
      titlePaths,
      deviceId,
      contentId,
      sensorId,
      startDate,
      endDate,
      interval,
      name,
      title
    ) {
      useSensoreData(deviceId, sensorId, startDate, endDate, interval)
        .then((response) => {
          // 
          const { status, message, data } = response["data"];
          if (status === 0) {
            // 
            let settings = new RawDataSettings();
            data.flags = new Object();
            data.flags = this.flags.flags;
            data.category = new Object();
            data.category = this.flags.category;
            settings.setGrowthGraph(
              title,
              this.xTitle[0].text,
              this.yTitle[contentId - 1].text,
              data
            );
            // 
            let graphPanel = new RawDataGraphPanel();
            // 
            graphPanel.setPanelTitle(titlePaths);
            // 
            graphPanel.setGraphData(settings);
            // 
            this.$refs.chr.addGraph(graphPanel);
          } else {
            throw new Error(message);
          }
        })
        .catch((error) => {
          //
          console.log(error);
        });

      this.flags = new LabelFlags();
      this.flags.getSensorGraphDataByInterval(startDate, endDate, interval);
    },
  },
};
</script>
  
