export class LabelFlags {
    constructor() {
        this.TEN = 1;         // 10
        this.THIRTY = 2;      // 30
        this.HOUR = 4;        // 1
        this.HOUR2 = 8;       // 2
        this.HOUR4 = 16;      // 4
        this.HOUR6 = 32;      // 6
        this.HOUR12 = 64;     // 12
        this.HOUR_0 = this.HOUR | this.HOUR2 | this.HOUR4 | this.HOUR6 | this.HOUR12;
        this.DAY = 128;       // 1
        this.DAYS5 = 256;     // 530
        this.DAYS10 = 512;    // 1030
        this.DAYS15 = 1024;   // 1530
        this.MONTH = 2048;    // 1
        this.DAY_1 = this.DAY | this.DAYS5 | this.DAYS10 | this.DAYS15 | this.MONTH;
        this.flags = [];
        this.category = [];

    }

    // 
    getSensorGraphDataByInterval(startDate, endDate, minutes) {
        // 
        let startTime = new Date(startDate);
        startTime.setUTCHours(0, 0, 0, 0);  // HOUR_OF_DAY, MINUTE, SECOND, MILLISECOND0

        let seekTime = startTime.getTime();

        // 
        let endTime = new Date(endDate);
        endTime.setUTCDate(endTime.getUTCDate() + 1);
        endTime.setUTCHours(0, 0, 0, 0);
        let endTimeMillis = endTime.getTime();

        // 
        let interval = 60000 * minutes;
        let cal = new Date();
        while (seekTime < endTimeMillis) {
            // 
            cal.setTime(seekTime);
            //  "MM/dd hh:mm"  (UTC)
            let formattedDate = this.formatDateUTC(cal);
            this.category.push(formattedDate);  // days
            let flag = this.getFlag(cal);
            this.flags.push(flag);
            seekTime += interval;
        }
    }

    // 
    getFlag(cal) {
        let minutes = cal.getUTCMinutes();
        if (minutes !== 0) {
            if (minutes === 30) {
                return this.THIRTY;
            } else {
                return 0;
            }
        }

        let flag = 6;  // THIRTY|HOUR
        let hour = cal.getUTCHours();
        if (hour === 0) {  // 0
            flag |= this.HOUR_0;
        } else {
            flag |= this.HOUR;
            if (hour % 2 === 0) flag |= this.HOUR2;
            if (hour % 4 === 0) flag |= this.HOUR4;
            if (hour % 6 === 0) flag |= this.HOUR6;
            if (hour % 12 === 0) flag |= this.HOUR12;
            return flag;
        }

        let date = cal.getUTCDate();
        if (date === 1) {  // 1
            return flag | this.DAY_1;
        } else {
            flag |= this.DAY;
            if (date !== 30) {
                if (date % 5 === 0) flag |= this.DAYS5;
                if (date % 10 === 0) flag |= this.DAYS10;
                if (date % 15 === 0) flag |= this.DAYS15;
            }
            return flag;
        }
    }
    formatDateUTC(cal) {
        let month = (cal.getUTCMonth() + 1).toString().padStart(2, '0');  //  (0)
        let day = cal.getUTCDate().toString().padStart(2, '0');            //  (0)
        let hours = cal.getUTCHours().toString().padStart(2, '0');         //  (0)
        let minutes = cal.getUTCMinutes().toString().padStart(2, '0');     //  (0)
        return `${month}/${day} ${hours}:${minutes}`;                      // "MM/dd hh:mm" 
    }
}
