const useGetAllDataLoadStatus = () => {
  const config = {
    params: {},
  };
  return axios.get("/bulk/load/info", config);
  /* 
    {
    private String name;
    private Long id;
    private String status;
    private String loadTime;
    private String updateTime;
  }
  */
};
// ======================================================
// 
// ======================================================
const useGetLog = (deviceId, type) => {
  const config = {
    params: { deviceId, type },
  };
  return axios.get("/bulk/load/log", config);
  /* 
    {
    private String date;
    private String message;
    }
  */
};
// ======================================================
// 
// ======================================================
const usePostRequest = (data) => {
  /*
    {
    deviceId : "ID"
    }
  */
  const config = {
    params: {},
  };
  return axios.post("/bulk/load/device", data, config);
};


export {
  useDeviceList,
  useDeviceShortList,
  useDeviceInfoRemove,
  useDeviceInfo,
  useDeviceInfoAdd,
  useDeviceInfoUpdate,
  useDeviceMastersAPI,
  useGetSchedule,
  useGetAllDataLoadStatus,
  useGetLog,
  usePostRequest
};
