<template>
  <v-app>
    <v-container style="text-align: center">
      <v-card elevation="3" width="100%">
        <div class="cm_Title"> {{ title }} </div>
        <div style="font-size: 10pt">
          
        </div>
        <v-data-table
          :headers="headers"
          :items="selectedDataList"
          mobile-breakpoint="0"
          :sort-by.sync="sortBy"
          :sort-desc.sync="sortDesc"
        >
          <template v-slot:[`item.url`]="{ item }">
            <v-img :src="item.url" width="30px"></v-img>
          </template>
        </v-data-table>
      </v-card>
    </v-container>
  </v-app>
</template>
<script>
import { useSum } from "@/api/Top";
export default {
  props: { shared /** MountController */: { required: true } },
  data() {
    return {
      title: "",
      selectedDataList: [],
      sortBy: "value",
      sortDesc: false,
      headers: [
        { text: "", value: "name", sortable: true, width: "30%" },
        {
          text: "",
          value: "sensorName",
          sortable: true,
          width: "20%",
        },
        { text: "", value: "value", sortable: true, width: "10%" },
        { text: "", value: "date", sortable: true, width: "30%" },
        { text: "", value: "url", sortable: false, width: "10%" },
      ],
      headerMap: {
        1: "()",
        2: "(RH)",
        3: "(W/)",
        4: "(g/h)",
        5: "(m)",
        6: "(raw counts)",
        7: "(pF)",
        8: "()",
        9: "-()",
        10: "-()",
      },
    };
  },

  mounted() {
    this.shared.mount(this);
  },

  methods: {
    initialize: function (koumoku) {
      this.selectedDataList = [];
      this.title = this.headerMap[koumoku.variable];
      useSum(koumoku.variable)
        .then((response) => {
          //
          const results = response["data"];
          for (const item of results.data) {
            var n = 2; // n
            item.value =
              Math.floor(item.value * Math.pow(10, n)) / Math.pow(10, n);
            var selected = {
              name: item.name,
              sensorName: item.sensorName,
              value: item.value,
              date: item.castedAt.substring(0, 19),
              url: "http:" + item.url,
            };
            this.selectedDataList.push(selected);
          }
        })
        .catch((error) => {
          //
          console.log(error);
        });
    },
  },
  created: function () {
    this.sourceData = this.$store.getters.sourceData;
  },
};
</script>
